<template>
  <div>
    <v-layout>
      <v-flex>
        <v-jumbotron color="primary" dark height="150px">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h3 class="display-1">{{ $store.state.nabi.item.name }}</h3>
                <span>{{ $store.state.nabi.item.description }}</span>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
      </v-flex>
    </v-layout>

    <v-container>
      <v-layout v-for="(item, index) in kisah" :key="index">
        <v-flex :id="item.key">
          <h1> {{ item.title }} </h1>
          <p v-html="item.content"></p>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  methods: {
    getKisah (key) {
      this.$firebase.database().ref('kisah')
    }
  },
  computed: {
    ...mapGetters({
      kisah: 'kisahNabi/kisah'
    })
  },
  mounted () {
    this.$store.commit('viewKisah/url', window.location.href)
    this.$store.commit('viewKisah/showIconDrawer', true)
    this.$store.dispatch('nabi/getLocal', this.$route.params.id)
    this.$store.dispatch('kisahNabi/getsLocal', this.$route.params.id)
    this.$store.dispatch('nabi/getOnline', this.$route.params.id)
    this.$store.dispatch('kisahNabi/getsOnline', this.$route.params.id)
  },
  beforeDestroy () {
    this.$store.commit('viewKisah/showIconDrawer', false)
  }
}
</script>




