export default {
  namespaced: true,
  state: {
    items: [],
    item: {}
  },
  mutations: {
    items: (state, data) => {
      state.items = data
    },
    itemPush: (state, data) => {
      state.items.push(data)
    },
    item: (state, data) => {
      state.item = data
    }
  },
  getters: {
    items: state => {
      return state.items.sort((a, b) => {
        return a.order - b.order
      })
    }
  },
  actions: {
    getsOnline (context) {
      return this._vm.$firebase.database().ref('nabi').once('value', snap => {
        context.commit('items', [])
        for (let key in snap.val()) {
          // masukin ke idb
          this._vm.$idb.nabi.setItem(key, snap.val()[key]).then(nabi => {
            if (nabi) {
              nabi.key = key
              // store ke nabi
              context.commit('itemPush', nabi)
            }
          })
        }
      })
    },
    getsLocal (context) {
      context.commit('items', [])
      return this._vm.$idb.nabi.iterate((value, index, iterationNumber) => {
        if (value) {
          value.key = index
          context.commit('itemPush', value)
        }
      })
    },
    getOnline (context, key) {
      return this._vm.$firebase.database().ref('nabi').child(key).once('value', snap => {
        if (snap.val()) {
          this._vm.$idb.nabi.setItem(key, snap.val()).then(nabi => {
            context.commit('item', snap.val())
          })
        }
      })
    },
    getLocal (context, key) {
      return this._vm.$idb.nabi.getItem(key).then(val => {
        if (val) {
          val.key = key
          context.commit('item', val)
        }
      })
    }
  }
}



