<template>
  <div>
    <v-jumbotron color="primary" dark height="150px">
      <v-container fill-height>
        <v-layout align-center>
          <v-flex text-xs-center>
            <h3 class="display-1">Saran ?</h3>
          </v-flex>
        </v-layout>
      </v-container>
    </v-jumbotron>
    <v-container>
      <v-layout>
        <v-flex>
          <v-form v-model="valid" ref="form" lazy-validation @submit.prevent="onSubmit">
            <v-text-field
              label="Name"
              v-model="form.name"
              :rules="rules.name"
              :counter="35"
              required
              :disabled="disabled.name"
            ></v-text-field>
            <v-text-field
              label="Email"
              v-model="form.email"
              :rules="rules.email"
              :counter="35"
              required
              :disabled="disabled.email"
            ></v-text-field>
            <v-text-field
              label="Saran"
              v-model="form.saran"
              :rules="rules.saran"
              :counter="500"
              required
              textarea=""
              :disabled="disabled.saran"
            ></v-text-field>
          </v-form>
        </v-flex>
      </v-layout>
      <v-layout>
        <v-flex>
          <v-btn block="" color="red" dark @click="reset()"
          :disabled="disabled.btnReset"> Clear </v-btn>
        </v-flex>
        <v-flex>
          <v-btn block="" color="primary" @click="send()"
          :disabled="disabled.btnSend"
          :loading="loadings.btnSend"> Send </v-btn>
        </v-flex>
      </v-layout>
    </v-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      valid: true,
      rules: {
        name: [
          v => !!v || 'Name is required',
          v => (v && v.length <= 35) || 'Name must be less than 35 characters'
        ],
        email: [
          v => !!v || 'E-mail is required',
          v => (v && v.length <= 35) || 'Email must be less than 35 characters',
          v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
        ],
        saran: [
          v => !!v || 'Saran is required',
          v => (v && v.length <= 500) || 'Saran must be less than 500 characters'
        ]
      },
      form: {
        name: null,
        email: null,
        saran: null
      },
      disabled: {
        name: false,
        email: false,
        saran: false,
        btnSend: false,
        btnReset: false
      },
      loadings: {
        btnSend: false
      }
    }
  },
  methods: {
    reset () {
      this.$refs.form.reset()
    },
    send (response) {
      if (this.$refs.form.validate()) {
        this.disabledAll(true)
        this.loadings.btnSend = true
        this.$firebase.database().ref('saran').push({
          name: this.form.name,
          email: this.form.email,
          saran: this.form.saran,
          created_at: Date.now()
        }).then(() => {
          alert('Great day and thank you!')
          this.disabledAll(false)
          this.loadings.btnSend = false
          this.reset()
        }).catch(() => {
          alert('Oopps, failed to send!')
          this.disabledAll(false)
          this.loadings.btnSend = false
        })
      }
    },
    disabledAll (status) {
      this.disabled.name = status
      this.disabled.email = status
      this.disabled.saran = status
      this.disabled.btnSend = status
      this.disabled.btnReset = status
    }
  }
}
</script>




