<template>
  <div>
    <v-layout>
      <v-flex>
        <v-jumbotron color="primary" dark height="150px">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h3 class="display-1">LIST NABI</h3>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
      </v-flex>
    </v-layout>
    <v-layout>
      <v-flex>
        <v-list>
          <div v-for="(item, index) in nabi" :key="index">
            <v-list-tile :to="'/nabi/view/' + item.key">
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ item.name }}
                </v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider></v-divider>
          </div>
        </v-list>
      </v-flex>
    </v-layout>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  computed: {
    ...mapGetters({
      nabi: 'nabi/items'
    })
  },
  mounted () {
    this.$store.dispatch('nabi/getsLocal')
    this.$store.dispatch('nabi/getsOnline')
  }
}
</script>




