export default {
  namespaced: true,
  state: {
    toggleDrawer: false,
    showDrawer: true,
    showIconDrawer: true,
    menu: [
      {
        name: 'Halaman Depan',
        icon: 'dashboard',
        url: '/'
      },
      {
        name: 'Kisah Nabi',
        icon: 'dashboard',
        url: '/nabi/list'
      },
      {
        name: 'Saran',
        icon: 'dashboard',
        url: '/saran'
      }
    ]
  },
  mutations: {
    toggleDrawer: (state) => {
      state.toggleDrawer = !state.toggleDrawer
    },
    showDrawer: (state, status) => {
      state.showDrawer = status
    },
    showIconDrawer: (state, status) => {
      state.showIconDrawer = status
    }
  }
}



