import Vue from 'vue'
import Vuex from 'vuex'
import myInterface from '@/store/myInterface.js'
import nabi from '@/store/nabi.js'
import kisahNabi from '@/store/kisahNabi.js'
import viewKisah from '@/store/viewKisah.js'

Vue.use(Vuex)
export default new Vuex.Store({
  modules: {
    myInterface, nabi, kisahNabi, viewKisah
  }
})



