import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/dashboard/Index'
import Nabi from '@/components/nabi/Index'
import NabiList from '@/components/nabi/List'
import NabiView from '@/components/nabi/View'
import Saran from '@/components/saran/Index'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/nabi',
      name: 'Nabi',
      component: Nabi,
      children: [
        {
          path: 'list',
          name: 'NabiList',
          component: NabiList
        },
        {
          path: 'view/:id',
          name: 'NabiView',
          component: NabiView
        }
      ]
    },
    {
      path: '/saran',
      name: 'Saran',
      component: Saran
    }
  ]
})



