// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import store from '@/store/index.js'
import firebase from 'firebase'
import localforage from 'localforage'
import VueSocialSharing from 'vue-social-sharing'

Vue.use(Vuetify)
Vue.use(VueSocialSharing)

Vue.prototype.$firebase = firebase.initializeApp({
  apiKey: 'AIzaSyCVjnQVJhhYnhDGN42BNME13c9Em6hsktQ',
  authDomain: 'kisah-nabi-bdf39.firebaseapp.com',
  databaseURL: 'https://kisah-nabi-bdf39.firebaseio.com',
  projectId: 'kisah-nabi-bdf39',
  storageBucket: 'kisah-nabi-bdf39.appspot.com',
  messagingSenderId: '745489374072'
})
Vue.prototype.$idb = {
  nabi: localforage.createInstance({
    name: 'nabi'
  }),
  kisahNabi: localforage.createInstance({
    name: 'kisahNabi'
  }),
  kisah: localforage.createInstance({
    name: 'kisah'
  })
}

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>',
  store
})



