<template>
  <v-app>
    <v-navigation-drawer
      temporary=""
      fixed
      app
      v-model="$store.state.myInterface.toggleDrawer"
      v-if="$store.state.myInterface.showDrawer"
    >
      <v-jumbotron color="primary" dark height="150px">
        <v-container fill-height>
          <v-layout align-center>
            <v-flex text-xs-center>
              <h3 class="display-1">Menu</h3>
              <span>:D</span>
            </v-flex>
          </v-layout>
        </v-container>
      </v-jumbotron>
      <v-list>
        <div v-for="(item, index) in $store.state.myInterface.menu" :key="index">
          <v-list-tile :to="item.url">
            <v-list-tile-action>
              <v-icon>{{ item.icon }}</v-icon>
            </v-list-tile-action>
            <v-list-tile-content>
              <v-list-tile-title>{{ item.name }}</v-list-tile-title>
            </v-list-tile-content>
          </v-list-tile>
          <v-divider></v-divider>
        </div>
      </v-list>
    </v-navigation-drawer>
    <v-toolbar app color="primary" dark>
      <v-toolbar-side-icon 
      @click.stop="$store.commit('myInterface/toggleDrawer')"
      v-if="this.$store.state.myInterface.showIconDrawer">
      </v-toolbar-side-icon>
      <v-toolbar-title>{{ title }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-side-icon 
      @click.stop="$store.commit('viewKisah/toggleDrawer')"
      v-if="$store.state.viewKisah.showIconDrawer">
      </v-toolbar-side-icon>
    </v-toolbar>
    <v-content>
      <router-view/>
    </v-content>
    <drawer-view-kisah></drawer-view-kisah>
  </v-app>
</template>

<script>
import drawerViewKisah from '@/components/nabi/Drawer'
export default {
  components: {
    drawerViewKisah
  },
  data () {
    return {
      drawer: false,
      title: 'Kisah Nabi'
    }
  },
  name: 'App'
}
</script>



