export default {
  namespaced: true,
  state: {
    toggleDrawer: false,
    showIconDrawer: false,
    url: null
  },
  mutations: {
    toggleDrawer: state => {
      state.toggleDrawer = !state.toggleDrawer
    },
    showIconDrawer: (state, status) => {
      state.showIconDrawer = status
    },
    url: (state, data) => {
      state.url = data
    }
  }
}



