<template>
  <v-navigation-drawer
    temporary=""
    fixed
    app
    right=""
    v-model="$store.state.viewKisah.toggleDrawer"
  >
    
    <v-tabs
      v-model="active"
      color="primary"
      dark
      centered=""
    >
      <v-tab
        href="#view-kisah-list"
        ripple
      >
        kisah
      </v-tab>
      <!-- <v-tab
        href="#view-kisah-komentar"
        ripple
      >
        komentar
      </v-tab> -->
      <v-tab
        href="#view-kisah-share"
        ripple
      >
        Share
      </v-tab>

      <v-tab-item id="view-kisah-list">
        <v-jumbotron color="primary" dark height="100px">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h1>Akses Cepat</h1>
                <span>Mantap!</span>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
        <v-list>
          <div v-for="(item, index) in $store.state.kisahNabi.kisah" :key="index">
            <v-list-tile @click="goTo(item.key)">
              <v-list-tile-content>
                <v-list-tile-title>{{ item.title }}</v-list-tile-title>
              </v-list-tile-content>
            </v-list-tile>
            <v-divider></v-divider>
          </div>
        </v-list>
      </v-tab-item>
      <!-- <v-tab-item id="view-kisah-komentar">
        <v-jumbotron color="primary" dark height="100px">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h1>Komentar</h1>
                <span>Keren!</span>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
        <komentar></komentar>
      </v-tab-item> -->
      <v-tab-item id="view-kisah-share">
        <v-jumbotron color="primary" dark height="100px">
          <v-container fill-height>
            <v-layout align-center>
              <v-flex text-xs-center>
                <h1>kasih tau temen2 kamu.</h1>
                <span>good day!</span>
              </v-flex>
            </v-layout>
          </v-container>
        </v-jumbotron>
        <social-sharing :url="$store.state.viewKisah.url"></social-sharing>
      </v-tab-item>
    </v-tabs>
  </v-navigation-drawer>
</template>

<script>
import socialSharing from '@/components/nabi/Sharing.vue'
import komentar from '@/components/nabi/Komentar.vue'
export default {
  components: {
    socialSharing, komentar
  },
  data () {
    return {
      active: null
    }
  },
  methods: {
    goTo (id) {
      this.$vuetify.goTo('#' + id, {
        duration: 500,
        offset: -65
      })
    }
  }
}
</script>




