export default {
  namespaced: true,
  state: {
    kisah: []
  },
  mutations: {
    kisah: (state, data) => {
      state.kisah = data
    },
    pushKisah: (state, data) => {
      state.kisah.push(data)
    }
  },
  getters: {
    kisah: state => {
      return state.kisah.sort((a, b) => {
        return a.order - b.order
      })
    }
  },
  actions: {
    getsOnline (context, key) {
      return this._vm.$firebase
        .database()
        .ref('kisah')
        .orderByChild('nabiKey')
        .equalTo(key)
        .once('value', snap => {
          let kisah = snap.val()
          context.commit('kisah', [])
          for (let key in kisah) {
            this._vm.$idb.kisahNabi.setItem(key, kisah[key]).then(val => {
              if (val) {
                val.key = key
                context.commit('pushKisah', val)
              }
            })
          }
        })
    },
    getsLocal (context, nabiKey) {
      context.commit('kisah', [])
      return this._vm.$idb.kisahNabi.iterate((value, index, iterationNumber) => {
        if (value.nabiKey === nabiKey) {
          value.key = nabiKey
          context.commit('pushKisah', value)
        }
      })
    }
  }
}



